.header {
    border: 1px solid black;
}

.headUpTxt {
    background-color: black;
    text-align: center;
    color: white;
    padding: 15px 0;
    font-family: calibri;
}
.headBody {
    background-image: url(../img/ad1.jpg);
    background-size: cover;
    background-position: top;
    height: 600px;
}
.headUpNavMain {
    float: left;
    width: 100%;
    position: relative;
}
.headUpNav {
    float: right;
    width: auto;
    margin-top: 5px;
    margin-right: 50px;
}
.headUpNavLink {
    text-decoration: none;
    color: black;
    font-family: calibri;
    font-weight: 500;
}
.headUpNav span {
    cursor: pointer;
    margin-left: 15px;
    font-weight: 600;
}
.headUpNavTitle {
    position: absolute;
    left: 50%;
    font-family: calibri;
    transform: translateX(-50%);
    color: white;
    text-decoration: underline;
    top: 14px;
}

.headNav {
    float: left;
    width: 700px;
    padding: 5px 20px;
    border-radius: 50px;
    margin-top: 25px;
    margin-left: 50%;
    transform: translateX(-50%);
    background-color: white;
}

.headNav0 {
    float: left;
    width: calc(100% - 45px);
}
.headNav1 {
    float: left;
    width: 45px;
}
.headNav01 {
    display: inline-block;
    width: 24%;
    border-right: 1px solid black;
}
.headNav0111 {
    display: inline-block;
    width: 24%;
}
.headNav011 {
    font-size: 17px;
    font-family: 'calibri';
    font-weight: 600;
}
.headNav012 {
    font-size: 13px;
    font-family: 'calibri';
    font-weight: 500;
}
.headSideBar {
    background-color: white;
    position: absolute;
    width: 200px;
    right: 20px;
    border-radius: 6px;
    padding: 10px 0;
}
.headSideBar p {
    padding: 6px 12px;
    cursor: pointer;
    font-family: calibri;
}
.headSideBar p:hover {
    background-color: rgba(232, 232, 238, 0.585);
    font-weight: 600;
}
hr {
    background-color: rgb(233, 233, 233);
}


.headBodyExplore {
    float: left;
    width: 730px;
    margin-top: 65px;
    padding-top: 20px;
    margin-left: 50%;
    transform: translateX(-50%);
    line-height: 40px;
}
.headBodyTxt p {
    font-size: 2.5em;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
}
.headBodyExploreBtn {
    float: left;
    margin-top: 25px;
    border-radius: 8px;
    background-color: white;
    font-weight: 500;
    padding: 0px 15px;
    font-size: 16px;
    cursor: pointer;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.Closebtn {
    margin-bottom: 10px;
}
.Closebtn div {
    float: right;
    margin-right: 20px;
    margin-top: 20px;
    cursor: pointer;
    font-family: calibri;
}
.Closebtn div:hover {
    font-weight: 600;
}
.signupPage {
    position: fixed;
    width: 100%;
    height: 100vh;
    backdrop-filter: saturate(180%) blur(20px);
    background-color: rgba(191, 191, 191, 0.456);
    top: 0;
}
.sign0 {
    float: left;
    width: 800px;
    margin-left: 50%;
    transform: translateX(-50%);
    height: 500px;
    background-color: white;
    margin-top: 120px;
}
.sign01 {
    float: left;
    width: 30%;
    height: 100%;
    background-image: url(../img/ad.jpg);
    background-size: cover;
    background-position: top;
    font-family: calibri;
}
.sign011 {
    margin-top: 50px;
    font-size: 32px;
    font-weight: 700;
    color: white;
    padding: 0 20px;
}
.sign012 {
    margin-top: 5px;
    font-size: 14px;
    color: white;
    padding: 0 20px;
}
.sign02 {
    float: left;
    width: 70%;
    height: 100%;
}
.sign021 {
    float: left;
    width: 90%;
    margin-left: 50%;
    transform: translateX(-50%);
    border-bottom: 1px solid rgb(138, 136, 136);
    padding-bottom: 5px;
    margin-top: 30px;
}
.sign021 input {
    width: calc(100% - 50px);
    border: none;
    outline: none;
}
.sign021 span {
    width: 30px;
}
.sign02 button {
    float: left;
    width: 90%;
    margin-left: 50%;
    transform: translateX(-50%);
    background-color: rgb(141, 0, 248);
    color: white;
    margin-top: 25px;
    padding: 8px 0;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 4px;
}
.sign02 h4 {
    float: left;
    width: 100%;
    text-align: center;
    margin-top: 25px;
    font-family: calibri;
    font-size: 14px;
}
.sign02 h5 {
    float: left;
    width: 100%;
    text-align: center;
    margin-top: 60px;
    font-family: calibri;
    font-size: 14px;
}

.sign022 {
    float: left;
    width: 100%;
    margin-top: 60px;
}
.sign0221 {
    width: 25%;
    float: left;
}
.sign022 img {
    width: 55px;
    height: 55px;
    margin-left: 50%;
    border-radius: 50px;
    transform: translateX(-50%);
}
.sign022 p {
    font-size: 15px;
    font-family: calibri;
    font-weight: 600;
    color: rgb(105, 105, 105);
}