.container {
    float: left;
    width: 100%;
    font-family: calibri;
}
.heading {
    float: left;
    width: calc(100% - 40px);
    margin-top: 20px;
    padding: 10px 20px;
    font-family: calibri;
    font-size: 20px;
    font-weight: 600;
}

/* popular destination */
.PopularDestCont {
    float: left;
    width: 100%;
    overflow: hidden;
    overflow-x: scroll;
    white-space: nowrap;
}
.PopularDestSin {
    width: 300px;
    display: inline-block;
    margin-left: 20px;
}
.PopularDestSin img {
    width: 300px;
    height: 200px;
    border-radius: 4px 4px 0 0;
}
.PopularDestSin p {
    font-size: 15px;
    margin-top: 5px;
    font-weight: 600;
}

/* best deals */
.BestDealsCont {
    float: left;
    width: 100%;
    overflow: hidden;
    overflow-x: scroll;
    white-space: nowrap;
}
.BestDealsSin {
    width: 400px;
    display: inline-block;
    margin-left: 20px;
}
.BestDealsSin img {
    width: 400px;
    height: 350px;
    border-radius: 4px 4px 0 0;
}
.BestSinP1 {
    font-size: 20px;
    font-weight: 600;
}
.BestSinP2 {
    font-size: 15px;
    color: rgb(57, 57, 57);
}
.BestSinP3 {
    font-size: 13px;
    font-weight: 600;
    color: rgb(49, 49, 49);
}
.BestSinP5 {
    font-size: 17px;
    font-weight: 700;
    text-align: right;
    margin-top: -20px;
}
.BestSinP6 {
    font-size: 14px;
    font-weight: 500;
    text-align: right;
}
.BestDealsTxt0 {
    float: left;
    width: 100%;
}
.BestDealsTxt01, .BestDealsTxt02 {
    float: left;
    width: 50%;
}

